import Vue from 'vue'
import Router from 'vue-router'
import sy from './a.vue'
import sy1 from './b.vue'
import sy2 from './jd.vue'
import sy3 from './lx.vue'
import sy4 from './xw.vue'
//import sy5 from './dl.vue'
import cat from './cat.vue'
import shop from './shop.vue'
import cat_info from './cat_info.vue'
import xw from './xw.vue'
import read from './read.vue'
Vue.use(Router)
export default new Router({
	routes:[
		{
			path:'/xw',
		   name:'xw',
		   component:xw
		},
		{
		   path:'/read',
		   name:'read',
		   component:read
		},
	   {
		   path:'/',
		   name:'sy',
		   component:sy
	   },
	   {
		path:'/b',
		name:'sy1',
		component:sy1
	   },
	   {
		path:'/jd',
		name:'sy2',
		component:sy2
	   },
	   {
		path:'/lx',
		name:'sy3',
		component:sy3
	   },
	   {
		path:'/xw',
		name:'sy4',
		component:sy4
	   },
	   {
	   	path:'/cat',
	   	name:'cat',
	   	component:cat
	   },
	   {
	   	path:'/shop',
	   	name:'shop',
	   	component:shop
	   },
	   {
	   	path:'/cat_info',
	   	name:'cat_info',
	   	component:cat_info
	   },
	]
})






