<template>
	<div>
		<div class="top_header"></div>
		<div class="czh_teas">
			<div class="czh_wz">
				<a href="javascript:;">茶叶</a><span class="breadcrumb-text" style="color:#90af6b;font-size:14px;font-family:;margin-right:0px;"> &gt; </span>
				<a href="javascript:;">全部</a><span class="breadcrumb-text" style="color:#90af6b;font-size:14px;font-family:;margin-right:0px;"> &gt; </span>
				<a href="javascript:;">茶</a><span class="breadcrumb-text" style="color:#90af6b;font-size:14px;font-family:;margin-right:0px;"> &gt; </span><span style="color:#90af6b;">茶</span>
			</div>
		</div>
		<div class="czh_tupianc" v-for="i in g_str1">
			<div class="czh_hxu">
				<div class="g_nl_top" :style="{backgroundImage: 'url(' +i.image+ ')'}" @mouseover="jinru()" @mouseout="yichu()" @mousemove="yidong()">
					<div id="moveBox"></div>
					<div class="g_nl_big" :style="{backgroundImage: 'url(' +i.image+ ')'}"></div>
				</div>
				<div class="czh_smill_boxq">
					<div class="czh_simle_tu">
						<img :src="i.image" alt="">
					</div>
				</div>
				<div class="czh_right_nr">
					<!-- 茶名 -->
					<h1 class="czh_h1n">{{i.title}}</h1>
					<div class="fg-div small"></div>
					<div class="odmsadas">
						<!-- 价钱 -->
						<p class="czh_price"><span class="amount">¥{{i.pice}}</span></p>
					</div>
					<div itemprop="description" class="description">
						<p style="line-height: 20px; font-size:13px; color:#939393;">{{i.cat_info}}</p>
					</div>
					<div class="product_meta">
						<div class="dsada">
							<span class="sku_wrapper">商品编号： <span class="sku" itemprop="sku">{{i.b}}</span>&nbsp;</span>
						</div>
						<div class="dmsamoda">
							<span class="posted_in">分类：</span>
						</div>
					</div>
					<div class="czh_start">
						<div class="czh_gw">
							<button class="gwu">
                                <a href="javascript:;" @click="a_str(g_str1[0])" style="">
                                    加入购物车
                                </a>
                                </button>
						</div>
						<div class="czh_input_jj">
							<div class="quantity buttons_added">
								<input type="button" value="-" class="minus" @click="btn">
								<input type="text" data-default-value="" step="1" min="1" name="quantity" v-model="num" title="数量" class="input-text qty text" autocomplete="off" size="4">
								<input type="button" value="+" class="plus" @click="num++">
							</div>
						</div>
					</div>
					<div class="czh_bottom_wc">
						<span class="iconfont">&#xe607;</span>
						<span>收藏</span>
					</div>
				</div>
			</div>
			<div class="czh_boxdasda">
				<div class="czh_miashu">
					描述
				</div>
				<div class="czh_content">
					{{i.cat_info}}
				</div>
				<div class="czh_sm_pjds">
					<img :src="i.cat_img" alt="">
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import { mapGetters, mapActions } from 'vuex'
	export default {
		data() {
			return {
				arr: [],
				num: 1
			}
		},
		// watch:{
		// 	num(a,b){
		// 		// console.log(a,b)
		// 		g_str1[0].n==a
		// 	}
		// },
		computed: {
			...mapGetters(['g_str1', 'g_str'])
		},
		methods: {
			...mapActions(['a_str1', 'a_str']),
			btn() {
				if(this.num == 0) {
					this.num == 1
				} else {
					this.num--
				}
			},
			jinru() {
				var moveBox = document.querySelector('#moveBox')
				var rightBox = document.querySelector('.g_nl_big')
				var leftBox = document.querySelector('.g_nl_top')
				moveBox.style.display = 'block';
				rightBox.style.display = 'block';
				this.maxX = leftBox.clientWidth - moveBox.offsetWidth;
				this.maxY = leftBox.clientHeight - moveBox.offsetHeight;
				this.minX = leftBox.clientLeft
				this.minY = leftBox.clientTop

			},
			yichu() {
				var moveBox = document.querySelector('#moveBox')
				var rightBox = document.querySelector('.g_nl_big')
				moveBox.style.display = 'none';
				rightBox.style.display = 'none';
			},
			yidong(e) {
				var evt = e || event;
				var moveBox = document.querySelector('#moveBox')
				var rightBox = document.querySelector('.g_nl_big')
				var leftBox = document.querySelector('.g_nl_top')
				var moveX = evt.clientX - leftBox.clientWidth/2.5;
				var moveY = evt.clientY - leftBox.clientHeight/1.5;
				moveX = moveX < this.minX ? this.minX : moveX;
				moveX = moveX > this.maxX ? this.maxX : moveX;
				moveY = moveY < this.minY ? this.minY : moveY;
				moveY = moveY > this.maxY ? this.maxY : moveY;
				console.log(leftBox.clientWidth)

				moveBox.style.left = moveX + 'px';
				moveBox.style.top = moveY + 'px';

				rightBox.style.backgroundPositionX = -moveX * 2 + 'px';
				rightBox.style.backgroundPositionY = -moveY * 2 + 'px';
			}
		}
	}
</script>
<style>
	.g_nl_top {
		width: 420px;
		float: left;
		height: 420px;
	}
	
	#moveBox {
		width: 200px;
		height: 200px;
		background-color: rgba(100, 100, 100, 0.5);
		position: absolute;
		display: none;
	}
	
	.g_nl_big {
		position: absolute;
		left: 450px;
		top: 0;
		width: 420px;
		height: 420px;
		border: 1px solid #000000;
		background-size: 200% 200%;
		background-repeat: no-repeat;
		display: none;
	}
	
	.czh_sm_pjds {
		width: 100%;
		height: 621px;
	}
	
	.czh_sm_pjds img {
		width: 100%;
		height: 100%;
	}
	
	.czh_content {
		margin-top: 15px;
		line-height: 1.5;
		color: #777;
		font-size: 15px;
		margin-bottom: 15px;
	}
	
	.czh_miashu {
		height: 44px;
		line-height: 44px;
		font-size: 16px;
		color: #333;
		font-weight: bold;
	}
	
	.czh_boxdasda {
		max-width: 1024px;
		margin: 0 auto;
		min-height: 1000px;
		box-sizing: border-box;
		overflow: hidden;
		min-height: 500px;
	}
	
	.czh_simle_tu {
		width: 93px;
		height: 93px;
	}
	
	.czh_simle_tu img {
		width: 100%;
		height: 100%;
	}
	
	.czh_smill_boxq {
		padding-top: 10px;
		height: 109px;
		position: absolute;
		bottom: 0;
	}
	
	.czh_bottom_wc span {
		font-size: 12px;
		color: #666;
	}
	
	.czh_bottom_wc {
		margin-bottom: 20px;
		height: 20.8px;
		line-height: 20.8px;
	}
	
	.input-text {
		border-color: #ccc;
		background: none;
		background-image: none;
		border-style: solid;
		border-width: 1px;
		box-shadow: none;
		cursor: pointer;
		display: inline-block;
		filter: none;
		height: 42px;
		text-align: center;
		width: 32px;
		line-height: 28px;
		/* margin: 0 0 0 1px; */
		background: transparent;
	}
	
	.plus {
		background: none;
		background-image: none;
		border-style: solid;
		border-width: 1px;
		cursor: pointer;
		display: inline-block;
		filter: none;
		margin: 0 0 0 1px;
		text-align: center;
		border-bottom-color: #cccccc;
		border-top-color: #cccccc;
		height: 44px!important;
		width: 32px!important;
	}
	
	.buttons_added {
		overflow: hidden;
		min-width: 120px;
	}
	
	.minus {
		background: none;
		background-image: none;
		border-style: solid;
		border-width: 1px;
		box-shadow: none;
		cursor: pointer;
		display: inline-block;
		filter: none;
		height: 44px;
		margin: 0 5px 0 6px;
		text-align: center;
		width: 32px;
	}
	
	.czh_input_jj {
		width: 96px;
		height: 44px;
		float: left;
	}
	
	.czh_gw {
		width: 130px;
		height: 44px;
		float: left;
	}
	
	.gwu a {
		color: #fff;
	}
	
	.gwu {
		font-size: 16px;
		font-weight: normal;
		border-color: #519f10;
		background-color: #519f10;
		padding-top: 13px;
		padding-bottom: 13px;
		padding-left: 24px;
		padding-right: 24px;
		border: none;
		outline: none;
		box-sizing: border-box;
	}
	
	.czh_start {
		margin-bottom: 20px;
		overflow: hidden;
	}
	
	.dsada {
		height: 34.2px;
		padding: 7px 0;
		box-sizing: border-box;
		border-top: 1px dotted #ddd;
	}
	
	.dmsamoda {
		height: 34.2px;
		padding: 7px 0;
		box-sizing: border-box;
		border-top: 1px dotted #ddd;
	}
	
	.product_meta span {
		font-size: 14px;
		font-weight: normal;
		color: #777777;
		border-color: #dddddd;
	}
	
	.product_meta {
		margin-bottom: 20px;
	}
	
	.description {
		margin-bottom: 20px;
	}
	
	.czh_price {
		font-size: 22px;
		font-weight: normal;
		color: #444444;
		font-family: sans-serif;
		line-height: 22px;
	}
	
	.odmsadas {
		margin-bottom: 20px;
	}
	
	.fg-div {
		font-weight: normal;
		width: 30px;
		background-color: #dddddd;
		height: 3px;
		margin: 15px 0;
	}
	
	.czh_h1n {
		font-size: 22px;
		font-weight: normal;
		color: #444444;
	}
	
	.czh_right_nr {
		width: 51%;
		margin-bottom: 20px;
		float: right;
		height: 406px;
	}
	
	.dsadsad {
		float: left;
	}
	
	.dksaldkas {
		max-width: 1024px;
		margin: 0 auto;
		min-height: 1000px;
		padding: 0 40px;
		box-sizing: border-box;
		overflow: hidden;
	}
	
	.czh_hxu {
		max-width: 1024px;
		margin: 0 auto;
		min-height: 540px;
		box-sizing: border-box;
		overflow: hidden;
		position: relative;
	}
	
	.czh_tupianc {
		padding: 50px 0 50px;
		min-height: 600px;
	}
	
	.czh_wz {
		padding: 0 40px;
		box-sizing: border-box;
		color: #808080;
		font-size: 14px;
		height: 19.2px;
	}
	
	.czh_teas {
		height: 59.2px;
		padding: 20px 0;
		box-sizing: border-box;
		background: rgba(244, 244, 244, 0.5);
		font-size: 14px;
		color: #444;
	}
	
	.top_header {
		height: 80px;
	}
</style>


