			<h2 class='titlea'>种植基地</h2>
			<div class='b_txt'>
				<p>公司拥有完整、科学、质量管理体系，通过ISO14001，ISO22000国际质量体系认 证，有机产品认证，拥有大量先进、专业的生产设备和检测仪器，秉承“顾客至</p>
				<p>上，锐意进取”的经营理念，坚持“客户第一”的原则为广大客户提供优质的服务。</p>
			</div>
				<ul class='i_img'>
					<li v-for="(i,$index) in img_arr" class="vl_pic_item" @click="pics_btn(i,$index)"><img :src="i" alt=""></li>
				</ul> 
		</div>
		<div class="pics_mubu">
			<button @click="closebtn">×</button>
			<button class="play_pause" @click="pausebtn"><i class="el-icon-video-pause iconfont" style="font-size:22px;">&#xe659;</i></button>
			<div class="photoes" v-for="(i,$index) in img_arr" :style="$index==index?{'opacity':'1'}:{'opacity':'0'}">
				<img :src="i" alt="">
			</div>
			<button class="prev" @click="prevpic"><i class="el-icon-arrow-left iconfont" style="font-size:22px;vertical-align: middle;">&#xe601;</i></button>
			<button class="next" @click="nextpic"><i class="el-icon-arrow-right iconfont" style="font-size:22px;vertical-align: middle;">&#xe603;</i></button>
			<div class="list_photoes">
				<div style="overflow: hidden;display: table;margin:auto;">
					<img v-for="(i,$index) in img_arr" :src="i" alt="" @click="index=$index">
				</div>
			</div>
		</div>
	</div>
</template>

<script>
		export default {
		methods:{
			pics_btn(i,index){
				console.log(i)
				var obody=document.querySelector('.pics_mubu')
				setTimeout(()=>{
					obody.style.display='block'
				},500)
				this.index=index
				this.timer=setInterval(()=>{
					this.nextpic()
				},3000)
				this.tiptype=true
			},
			pausebtn(){
				var pause=document.querySelector('.play_pause>i')
				if(this.tiptype){
					clearInterval(this.timer)
					this.timer=null;
					this.tiptype=false;
					pause.className='el-icon-video-play'
				}else{
					pause.className='el-icon-video-pause'
					this.timer=setInterval(()=>{
						this.nextpic()
					},2000)
					this.tiptype=true
				}
			},
			closebtn(){
				var obody=document.querySelector('.pics_mubu')
					obody.style.top='-100vh';
				setTimeout(()=>{
					obody.style.display='none'
					obody.style.top='0';
				},1000)
			},
			nextpic(){
				// this.photoes=this.img_arr[this.index]
				this.index++
				if(this.index>=this.img_arr.length){
					this.index=0
				}
			},
			prevpic(){
				this.index--
				if(this.index<=-1){
					this.index=this.img_arr.length-1
				}
			}
		},
		data() {
			return {
				timer:null,
				tiptype:false,
				photoes:'',
				index:0,
				vllogo: 'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvNzNlYjg4NTNhNmZkNTZiYWRhN2Q4ZjI4NjNkOWM1ODguanBn.jpg',
				img_arr: [
          'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvNWQ3ZTc0ZDI3NTEzM2MxZmZkYTVkZTFmY2FiNGFkZTctMzAweDMwMC5qcGc_p_p100_p_3D.jpg',
          
          'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvNzQwMmZhZTFiYWE3OTgxODA4MWYwM2I2N2UwZTU0ZDItMzAweDMwMC5qcGc_p_p100_p_3D.jpg',
          
          'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvOWVlYzA0NGVmYmNiZGQwYzE0M2QwYmZhMGY2MjNkMDUtMzAweDMwMC5qcGc_p_p100_p_3D.jpg',
          
          'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvMGQ5YWMxYWEzOWNmZmIzYzk4NDE0Njk4YzZmZGQwZjQtMzAweDMwMC5qcGc_p_p100_p_3D.jpg',
          
          'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvOWVkYTg5ZjIzMWEzOGFjODE5NmJiYmYyOTA5YWEyZjctMzAweDMwMC5qcGc_p_p100_p_3D.jpg',
          
          'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvMTI1ZmM2NTk4YWE0NmFlY2JkOWY1NDAzYzkwYjM3ODctMzAweDMwMC5qcGc_p_p100_p_3D.jpg',
          
          'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDQvNGRmMDFhNzRmYTBhZWFiODIyYmM1MThhY2M2ZWFhOTYtMzAweDMwMC5qcGc_p_p100_p_3D.jpg',
          
					'https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cHM6Ly81Y2RkM2FhNWM0NmVhLnQ3My5xaWZlaXllLmNvbS9xZnktY29udGVudC91cGxvYWRzLzIwMTkvMDUvMmZlZDQ0NjA2NDI0N2FmZWI4YTk0ZmQ0NGRiMDc5NzktMzAweDMwMC5qcGc_p_p100_p_3D.jpg'
				],
			}
		}
	}
</script>

<style>
	.pics_mubu{
		width:100vw;
		height:100%;
		background:rgba(0,0,0,.8);
		z-index: 2;
		position: fixed;
		left:0;
		top:0;
		transition: 1s;
		display: none;
	}
	.photoes{
		position: absolute;
		width:960px;
		height:460px;
		left:50%;
		margin-left:-480px;
		top:50px;
		transition: .8s;
	}
	.photoes img{
		width:100%;
		height: 100%;
		transition: .8s;
	}
	.pics_mubu button{
		width:40px;
		height: 40px;
		background:#000;
		position: absolute;
		top:0;
		right:20px;
		text-align: center;
		line-height:40px;
		border:none;
		outline: none;
		color:#fff;
		font-size:36px;
	}
	.play_pause{
		width:40px;
		height: 40px;
		background:#000;
		position: absolute;
		top:0;
		left:50%;
		margin-left:-20px;
		text-align: center;
		line-height:40px;
		border:none;
		outline: none;
		color:#fff;
		font-size:36px;
		cursor: pointer;
	}
	.pics_mubu .prev{
		top:50%;
		margin-top:-20px;
		left:0;
    line-height:40px;
    vertical-align: middle;
	}
	.pics_mubu .next{
		top:50%;
		margin-top:-20px;
    line-height:40px;
    vertical-align: middle;
	}
	.list_photoes{
		position: absolute;
		width:960px;
		height:90px;
		left:50%;
		margin-left:-480px;
		bottom:20px;
		transition: .8s;
	}
	.list_photoes img{
		width: 60px;
		height: 60px;
		float: left;
		transition: .8s;
		vertical-align: bottom;
		position: relative;
		z-index: 10;
	}
	.list_photoes img:hover{
		/* width: 90px;
		height: 90px; */
		transform: scale(1.5);
	}
	
    .gallery-top {
      height: 80%;
      width: 100%;
    }
    .gallery-thumbs {
      height: 20%;
      box-sizing: border-box;
      padding: 10px 0;
    }
    .gallery-thumbs .swiper-slide {
      height: 100%;
      opacity: 0.4;
    }
    .gallery-thumbs .swiper-slide-thumb-active {
      opacity: 1;
    }

	.b_i{
		width: 40px;
		height: 40px;
		float: right;
	}
	.transition-box {
		margin-bottom: 10px;
		width: 200px;
		height: 100px;
		border-radius: 4px;
		background-color: #409EFF;
		text-align: center;
		color: #fff;
		padding: 40px 20px;
		box-sizing: border-box;
		margin-right: 20px;
	  }
	.tee_nav{
			overflow: hidden;
			height: 80px;
			vertical-align: middle;
			text-align: right;
			margin-right: 200px;
			font-size: 16px;
		}
	.tee_nav li{
		display: inline-block;
		line-height: 80px;
		margin:0 20px;
		color: #878787;
		font-size: 15px;
		cursor: pointer;
	}
	.titlea {
		width: 100%;
		line-height: 50px;
		text-align: center;
		font-size: 24px;
		font-weight: normal;
		margin-top: 80px;
		margin-bottom: 20px;
	}
	
	.b_txt {
		width: 100%;
		line-height: 26px;
		text-align: center;
		font-size: 16px;
		color: #878787;
	}
	
	.i_img {
		width: 100%;
		margin:0 auto;
		overflow: hidden;
		padding-top: 37px;
		box-sizing: border-box;
	}
	
	.i_img li {
		float: left;
		font-size: 0;
		cursor: pointer;
		margin-right: 10px;
		margin-bottom: 10px;
	}
	
	.i_img li img {
		width: 280px;
		height: 280px;
	}
	
	.i_img li:nth-child(4) {
		margin-right: 0;
	}
	
	.i_img li:nth-child(8) {
		margin-right: 0;
	}
	.h_t {
		color: #85bf41 !important
	}
</style>



