<template>
	<div style="padding-top: 80px;">
		<div v-if="g_str.length==0">
			<div class="g_cent">
				<p style="text-align: center;line-height: 50px;margin-top: 50px;font-size: 23px;">您的购物车是空的</p>
				<p style="text-align: center;height: 70px;padding-top: 20px;">
					<a href="#/cat" style="color: white;background: #519f10;padding: 10px 20px;">返回商店</a>
				</p>
			</div>
		</div>
		<div v-else>
			<!--{{g_str}}-->
			<div class="top">
				<p>"{{g_str[g_str.length-1].title}}"成功添加进您的购物车</p>
				<a href="#/cat">继续购物</a>
			</div>
			<div class="g_cent">
				<div style="width: 55%;float: left;">
					<div class="g_list">
						<p style="flex: 5;">商品</p>
						<p style="flex: 2;">价格</p>
						<p style="flex: 3;">数量</p>
						<p style="flex: 1;">合计</p>
					</div>
					<div class="c_cent">
						<div class="c_item" v-for="(i,$index) in g_str">
							<p style="flex: 1.5;" class="iconfont" @click="b_str(i.uid)">&#xe701;</p>
							<img style="float: left;" :src="i.image" alt="">
							<h6 style="float: left;">{{i.title}}</h6>
							<p style="float: left;">￥<span style="border: none;">{{i.pice}}</span></p>
							<p style="float: left;flex: 3;">
								<span @click="i.n--">-</span>
								<span>{{i.n}}</span>
								<span @click="i.n++">+</span>
							</p>
							<p style="float: left;">￥<span style="border: none;">{{i.n*i.pice}}</span></p>
						</div>
					</div>
				</div>
				<div class="right">
					<h5>购物车总计</h5>
					<div class="right_info">
						<p>购物车小计<span>￥{{z_num}}</span></p>
						<p>运费<span>免费送货</span></p>
						<p>订单总计<span>￥{{z_num}}</span></p>
					</div>
					<button style="background: #519f10;">前往收银台</button>
					<div>
						<h5>优惠卷：</h5>
						<input type="text" placeholder="优惠卷代码">
						<button style="background: #90af6b;">使用优惠卷</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters, mapActions } from 'vuex'
	export default {
		data() {
			return {
				as: 1,
				arr: [],
			}
		},
		computed: {
			...mapGetters(['g_str', 'z_num'])
		},
		methods: {
			...mapActions(['b_str'])
		}
	}
</script>

<style>
	.c_item p span {
		border: solid 1px #ccc;
		padding: 0 3px;
	}
	
	.c_item p {
		flex: 2;
		line-height: 50px;
	}
	
	.c_item h6 {
		flex: 3;
		padding-left: 5px;
		line-height: 50px;
	}
	
	.c_item {
		height: 70px;
		display: flex;
		padding: 10px 0;
		border-bottom: solid 1px #ccc;
		box-sizing: border-box;
	}
	
	.c_item img {
		flex: 1;
		height: 50px;
	}
	
	.c_cent {
		width: 100%;
		min-height: 500px;
	}
	
	.right input {
		width: 100%;
		height: 30px;
		margin: 10px 0;
		padding-left: 10px;
		box-sizing: border-box;
	}
	
	.right button {
		width: 100%;
		height: 40px;
		background: green;
		outline: none;
		border: none;
		color: white;
		font-size: 16px;
	}
	
	.right_info p span {
		float: right;
	}
	
	.right_info p {
		overflow: hidden;
		font-size: 14px;
		color: #666;
		line-height: 30px;
	}
	
	.right_info {
		padding: 5px 0;
		border-bottom: 3px solid #ccc;
		margin-bottom: 10px;
	}
	
	.right {
		float: right;
		width: 35%;
		margin-left: 30px;
	}
	
	.right h5 {
		border-bottom: 3px solid #ccc;
		padding: 4px;
		font-size: 16px;
	}
	
	.g_list {
		display: flex;
		width: 100%;
		border-bottom: 3px solid #ccc;
		padding: 4px;
	}
	
	.g_cent {
		margin: 0 auto;
		width: 70%;
		min-height: 500px;
		overflow: hidden;
		padding-top: 50px;
	}
	
	.top {
		width: 70%;
		margin: 0 auto;
		height: 44px;
		font-size: 16px;
		line-height: 40px;
		border: solid 1px #2f9a4c;
		padding-left: 30px;
		padding-right: 20px;
		box-sizing: border-box;
		margin-top: 30px;
		overflow: hidden;
		margin-bottom: 30px;
	}
	
	.top p {
		float: left;
		color: #2f9a4c;
	}
	
	.top a {
		float: right;
		background-color: #10ac62;
		color: white;
		width: 100px;
		text-align: center;
		height: 30px;
		line-height: 30px;
		margin-top: 5px;
	}
</style>


