import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

var state = {
	arr:[],
	arr1:[],
}

var getters = {
	g_str(state){
		return state.arr
	},
	g_str1(state){
		return state.arr1
	},
	z_num(state){
		var numn = 0
		for(var i=0;i<state.arr.length;i++){
			numn+=state.arr[i].n*state.arr[i].pice
		}
		return numn
	}
}

var mutations = {
	m_str(state,data){
		location.href='#/shop'
	},
	m_str1(state,data){
				var type = true
		        for (var i = 0; i < state.arr.length; i++) {
		            if (state.arr[i].uid == data.uid) {
		                type = false
		                state.arr[i].n++
		            }
		        }
		        if (type) {
		            data.n=1
		            state.arr.push(data)
		        }
			state.arr1=[]
			var type1 = true
	        for (var j = 0; j < state.arr1.length; j++) {
	            if (state.arr1[j].uid == data.uid) {
	                type1 = false
	                state.arr1[j].n++
	            }
	        }
	        if (type1) {
	            data.n=1
	            state.arr1.push(data)
	        }
		},
	no3(state,uid){
	  //移除
		 for(var i=0;i<state.arr.length;i++){
			if(state.arr[i].uid == uid){
				state.arr.splice(i,1)
			}
		}
	},
	
}

var actions={
	a_str({commit},num){
		commit('m_str',num)
	},
	a_str1({commit},num){
		commit('m_str1',num)
	},
	b_str({commit},uid){
		commit('no3',uid)
	},
}

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})



